import { Box, Button, createTheme, Stack, ThemeProvider, Typography } from "@mui/material";
import Heart from "../media/heart.png"
import Background from "../media/Background.jpg"
import {useState} from 'react';

export default function Home({change_page}) {
  function reset_game() {
    console.log("Reset!")
    localStorage.setItem('puzzleCompleted', false)
    localStorage.setItem('triviaCompleted', false)
    localStorage.setItem('colorCompleted', false)
    localStorage.setItem('wordCompleted', false)
    localStorage.setItem('mazeCompleted', false)
    localStorage.setItem('finalCompleted', false)
  }
  return (
    <div style={{  
      width: "100%",
      height: "100%",
      backgroundImage: "url(" + Background + ")",
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat'
    }}>
      <Box alignContent="center" alignItems="center" sx={{ width: "100%", height: "100%", margin: "auto", width: "100%"}}>
        <Stack alignContent="center" alignItems="center" sx={{width: "100%"}} spacing={2}>
          <Button variant="contained" onClick={change_page} sx={{ width: "30%", height: "10%"}}>
            <Typography>
              Start game
            </Typography>
          </Button>
          <Button variant="contained" onClick={reset_game}>
            <Typography>
              reset
            </Typography>
          </Button>
        </Stack>
      </Box>
    </div>
  );
}
